@import 'styles/colorPalette.scss';

.TableRow{
    width: 100%;
    height: 100px;
    position: relative;
    overflow-y: auto;

    .TableCell{
        width: 100%;
        overflow-wrap: break-word;
     

     .data {
        display: flex;
        flex-direction: column;
        width: 100%;
        overflow-wrap: break-word;
     }   

    }
}


.TableRow:nth-of-type(even){
    background-color:$color-1;
}
.TableRow:nth-of-type(odd){
    background-color:white;
}