@import "styles/colorPalette.scss";

.root {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  padding-left: 5.8em;

  .container {
    margin-top: 3em;
    margin-right: 0.5em;
    width: 100%;
    height: auto;
    display: grid;
    flex-direction: row;
    z-index: 10;
    grid-template-rows: 0.1fr 1fr 0.4fr;

    > div:nth-child(1) {
      background-color: $color-4;
      width: 100%;
      height: auto;
      display: flex;
      justify-content: flex-end;
      align-items: flex-start;
      padding-bottom: 0.4em;
      padding-top: 1em;
      padding-right: 3em;

      .button {
        margin-top: 2em;
        background-color: $color-9;
        color: white;
        text-transform: initial;
        padding-left: 1.5em;
        padding-right: 1.5em;
        font-size: 0.8vw;
      }
    }

    .tables {
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;

      > div:nth-child(1) {
        background-color: $color-4;
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
      }
      > div:nth-child(2) {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: flex-start;
        padding: 10px;
        background-color: $color-4;
      }
    }

    .buttonDiv {
      background-color: transparent;
      width: 100%;
      height: auto;
      display: flex;
      justify-content: flex-end;
      align-items: flex-start;
      padding-top: 0.4em;
      padding-right: 0.5em;
      margin-top: 1em;
      .button {
        background-color: $color-10;
        color: white;
        text-transform: initial;
        padding-left: 2.5em;
        padding-right: 2.5em;
        font-size: 1vw;
      }
      .btnCancel {
        background-color: $color-10;
        color: white;
        text-transform: initial;
        padding-left: 2.5em;
        padding-right: 2.5em;
        font-size: 1vw;
      }
      .btnSave {
        background-color: $color-9;
        color: white;
        text-transform: initial;
        padding-left: 2.5em;
        padding-right: 2.5em;
        margin-left: 1.5em;
        font-size: 1vw;
      }
    }
  }
}
