@import "styles/colorPalette.scss";

.tableContainer {
  width: 100%;
  height: auto;
  position: relative;
  .tableRoot {
    width: 100%;
    height: auto;
    background-color: $color-2;
    display: flex;
    flex-direction: column;

    .tableHeader {
      width: 100%;
      height: auto;

      .tableRow {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;

        .tableCell {
          width: 100%;
          height: auto;
        }
        .tableCell:nth-child(4) {
          transform: translateX(20%);
        }
        .tableCell:nth-child(5) {
          transform: translateX(10%);
        }
      }
    }

    .tableBody {
      position: relative;
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;

      .inlineNormal {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;

        .inlineTableCell {
          width: 100%;
          height: auto;
        }
      }

      .editAndDelete {
        width: 5%;
        height: 20%;
        display: flex;
        flex-direction: row;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        position: absolute;
        right: 0;
        transform: translateX(-20%);
      }

      .tableRowEdit {
        widows: 100%;
        height: auto;
        display: flex;
        flex-direction: row;
        justify-content: center;
        text-align: center;

        .tableCellEdit {
          width: 100%;
          height: auto;
          display: flex;
          font-size: 12px;

          .textEdit {
            width: 90%;
            height: auto;
            display: flex;
            font-size: 12px;
            text-align: center;
          }
        }
      }
    }
  }
}
