@import "styles/colorPalette.scss";

.root {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;

    .wrapper {
        display: flex;
        flex-direction: row;
        width: 100%;
        height: auto;
        .numberFormat {
            width: 100%;
            height: 25px;
            .InputFieldTags {
                width: 100%;
                height: 25px;
                border-radius: 5px;
                border: 1px solid gray;
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                padding-left: 0.5em;
                color: $color-12;
                font-weight: 900;
                background-color: white;
                font-size: 0.9vw;
                font-weight: 500;

                background-color: white;
                input::-webkit-outer-spin-button,
                input::-webkit-inner-spin-button {
                    -webkit-appearance: none;
                    margin: 0;
                }
            }

            .InputFieldTagsError {
                width: 100%;
                height: 25px;
                border-radius: 5px;
                border: 1px solid red;
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                padding-left: 0.5em;
                color: $color-12;
                font-weight: 900;
                background-color: white;
                font-size: 0.9vw;
                font-weight: 500;

                background-color: white;
                input::-webkit-outer-spin-button,
                input::-webkit-inner-spin-button {
                    -webkit-appearance: none;
                    margin: 0;
                }
            }
        }

        .tags {
            width: 20%;
            height: 25px;
            text-align: center;
            left: 0;
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
            border-bottom: 1px solid gray;
            border-top: 1px solid gray;
            border-right: 1px solid gray;
            background-color: white;
            color: black;
            font-size: 16px;

            > label {
                width: 100%;
            }
        }

    }

    .errorField {
        font-size: 12px;
        margin-left: 0.5em;
        margin-top: 0.5em;
        text-transform: initial;
        color: red;
    }
}

.InputFields {
    width: 100%;
    height: 25px;
    border-radius: 5px;
    border: 1px solid gray;
    padding-left: 0.5em;
    color: $color-12;
    font-weight: 900;
    background-color: white;
    font-size: 0.9vw;
    font-weight: 500;

    background-color: white;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    /* Firefox */
    input[type="number"] {
        -moz-appearance: textfield;
    }
}

