.dropDownwithLabel {
    width: auto;
    height: auto;
    display: flex;
    flex-direction: column;

    > label {
        font-size: 8px;
        font-weight: bold;
    }

    .selectInput {
        width: 100%;
        height: 25px;
        border-radius: 5px;
        border: 1px solid gray;
        padding-left: 0.5em;
        color: $color-12;
        font-weight: 900;

        font-size: 0.9vw;
        font-weight: 500;

        background-color: white;

        .options {
            width: auto;
            height: 25%;
            overflow-y: scroll;
        }

        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
        }

        /* Firefox */
        input[type="number"] {
            -moz-appearance: textfield;
        }
    }

    .selectDisable {
        width: 100%;
        height: 25px;
        border-radius: 5px;
        border: 1px solid gray;
        padding-left: 0.5em;
        color: $color-12;
        font-weight: 900;
        font-size: 0.9vw;
        font-weight: 500;

        background-color: lightgrey;

        .options {
            width: auto;
            height: 25%;
            overflow-y: scroll;
        }

        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
        }

        /* Firefox */
        input[type="number"] {
