        }

        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
        }

        /* Firefox */
        input[type="number"] {
            -moz-appearance: textfield;
        }
    }

    .error {
        font-size: 12px;
        margin-left: 0.5em;
        margin-top: 0.5em;
        text-transform: initial;
        color: red;
    }
}

