@import "styles/color.scss";

.root {
  position: absolute;
  left: 0;
  top: 0%;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;

  > div {
    padding: 0;
  }
  > div:nth-child(1) {
    position: sticky;
    // width: 100%;
  }

  .headerContainer {
    position: relative;
    width: 100%;
    height: auto;
    display: grid;
    padding: 0;
    grid-template-rows: 1fr 1fr;
    grid-gap: 0.2em;
    grid-auto-rows: minmax(200px, auto);
    z-index: 10;

    .breadCrumbs {
      height: auto;
      padding: 20px;
    }

    .secondContainer {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;

      > div:nth-child(1) {
        padding: 20px;
        font-weight: bold;
        font-size: 24px;
        letter-spacing: 0.1em;
        font-family: Arial, Helvetica, sans-serif;
      }
      > div:nth-child(2) {
        flex: 1;
      }
    }
  }
}
