@import "styles/colorPalette.scss";

.root {
    width: 100%;
    .headerContainer {
        width: 100%;
        display: flex;
        justify-content: space-between;
        .title {
            color: $color-12;
            margin-bottom: 0;
        }
        .subTitle {
            color: $color-12;
            font-weight: normal;
            font-style: italic;
            margin-top: 0;
        }
        .ratingContainer {
            display: flex;
            align-items: flex-end;
            .ratingLabel {
                color: $color-12;
                font-weight: bold;
                .ratingScore {
                    color: $color-9;
                    font-weight: bold;
                    font-size: 1.7rem;
                }
            }
        }
    }
    .content {
        padding: 0;
        padding-top: 20px;

        .table {
            height: 200px;
            overflow-y: scroll;
        }

        .tableHead {
            .tableCell {
                color: $color-12;
            }
        }
        .tableBody {
            height: 100px;
            overflow-y: scroll;
            .tableCell {
                color: $color-12;
                .tableCellTitle {
                    color: $color-9;
                    font-weight: bold;
                    margin: 0;
                }
                .tableCellText {
                    margin: 0;
                    color: $color-12;
                    font-weight: normal;
                    font-size: 0.9rem;
                }
            }
            .tableCellFlex {
                display: flex;
                color: $color-12;
                justify-content: flex-start;
                align-items: center;
            }
        }
    }
}
