@import "styles/colorPalette.scss";
.fetching-indicator {
  font-size: 12px;
  color: grey;
  font-weight: 600;
}
.secondPartInputFields {
  display: grid;
  flex-direction: row;
  width: 100%;
  height: 100%;
  grid-template-rows: repeat(4, 1fr);
  grid-auto-rows: minmax(100px, auto);
  grid-gap: 0.5em;
  font-weight: 400;
  font-size: 15px;
  padding-top: 15px;
  padding-bottom: 10px;
  color: $color-12;

  .firstInputLayerFields {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100px;

    > div:nth-child(1) {
      width: 18%;
      height: auto;
      margin-right: 3em;
    }

    > div:nth-child(2) {
      width: 25.1%;
      height: auto;
      margin-right: 3em;
    }
    > div:nth-child(3) {
      width: 22.3%;
      height: auto;
      margin-right: 3em;
    }
  }

  .secondInputLayerFields {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100px;

    > div:nth-child(1) {
      width: 18%;
      height: auto;
      margin-right: 3em;
    }

    > div:nth-child(2) {
      width: 25.1%;
      height: auto;
      margin-right: 3em;
    }
    > div:nth-child(3) {
      width: 22.3%;
      height: auto;
      margin-right: 3em;
    }
  }

  .thirdInputLayerFields {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100px;

    > div:nth-child(1) {
      width: 18%;
      height: auto;
      margin-right: 3em;
    }

    > div:nth-child(2) {
      width: 25.1%;
      height: auto;
      margin-right: 3em;
    }
    > div:nth-child(3) {
      width: 10%;
      height: auto;
      margin-right: 3em;
    }
  }

  .clinicScheduleAndRoomNumber {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100px;

    > div:nth-child(1) {
      width: 46%;
      height: auto;
      margin-right: 3em;
    }

    > div:nth-child(2) {
      width: 10%;
      height: auto;
    }
  }
}
