.root {
    display: flex;
    width: 100%;
    flex-direction: column;
    position: relative;
  
    .label {
      padding-bottom: 5px;
    }
  
    .wrapper {
      display: flex;
      flex-direction: column;
      z-index: 1;
  
      .read-only-value {
        // font-weight: unset;
        padding: 5px;
      }
  
      .text-field {
        flex: 1;
        width: 180px;
        &.no-margin {
          margin-bottom: 0px;
        }
        margin-bottom: 23px;
        display: flex;
  
        .input {
          
          width: 100%;
          padding: 10px;
  
          input {
            padding: 0 !important;
          }
        }
  
        .date-button {
          display: flex;
          justify-content: center;
          align-items: center;
          min-width: 40px;
  
          button {
            padding: 8px;
          }
          .label {
            padding: 0;
          }
        }
  
        border: 1px solid transparent;
  
        &.focus {
          border-color: green;
        }
  
        // Errors
        &.has-error {
          border-color: red;
          margin-bottom: unset;
        }
  
        &.disabled {
          background-color: rgb(223, 223, 223);
        }
      }
  
      span.error {
        padding-top: 1px;
        color: red;
        font-size: 11px !important;
      }
    }
  
    .date {
      position: absolute;
      right: 0;
      bottom: 0;
      padding-right: 10px;
    }
  }
  