@import '../../../Assets/colorPallete/colorPallete.scss';

.root {
  position: absolute;
  left: 0;
  top: 0%;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;

  > div {
    padding: 0;
  }
  > div:nth-child(1) {
    position: sticky;
  }

  .FooterButtonContainer {
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    bottom: 0;
    height: auto;
    padding-bottom: 30px;

    .FooterButtonWrapper {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      margin-top: 2%;
      width: 100%;
      height: auto;
      background-color: rgba(0, 0, 0, 0.3);
      padding-left: 3em;

      > div:nth-child(1) {
        padding-top: 1.5em;
        padding-bottom: 1.5em;
        .disapproveButton {
          padding-top: 0.5em;
          padding-bottom: 0.5em;
          padding-right: 3em;
          padding-left: 3em;
          text-transform: capitalize;
        }
      }
      > div:nth-child(2) {
        flex: 1;
      }
      > div:nth-child(3) {
        display: flex;
        justify-content: space-evenly;
        padding-top: 1.5em;
        padding-bottom: 1.5em;
        flex-direction: row;
        margin-right: 10em;

        > div:nth-child(1) {
          display: flex;
        }

        > div:nth-child(1) {
          margin-left: 1em;
          .proceedButton {
            padding-top: 0.5em;
            padding-bottom: 0.5em;
            padding-right: 3em;
            padding-left: 3em;
            background-color: $color-9;
            color: $color-1;
            text-transform: capitalize;
          }
        }
      }
    }
  }

  .datepicker {
    padding-top: 40px;
    padding-bottom: 10px;
    padding-left: 20px;
    width: 300px;
    color: #192965;
  }

  .nature {
    padding-bottom: 10px;
    padding-left: 20px;
  }

  .medaffitype {
    padding-bottom: 10px;
    padding-left: 40px;
  }

  .affitype {
    padding-top: 30px;
    padding-bottom: 10px;
    padding-left: 40px;
  }

  .mnofield {
    width: 350px;
    padding-top: 20px;
    padding-bottom: 10px;
    padding-left: 40px;
    color: #192965;
  }

  .officeLocation {
    width: 300px;
    padding-top: 20px;
    padding-bottom: 10px;
    padding-left: 45px;
    color: #192965;
  }

  .radiogroup {
    .radio-hospital-affi {
      padding-left: 40px;
      padding-right: 170px;
    }

    .radio-dental-affi {
    }

    .radio-med-affi {
      padding-left: 40px;
      padding-right: 130px;
    }

    .radio-doctor-affi {
      padding-right: 200px;
    }

    .radio-special {
    }

    .radio-newaffi {
      padding-left: 40px;
      padding-right: 200px;
    }

    .radio-addaffi {
    }
  }
}

.date-reference-container {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  width: 580px;
}

.reference-sub-label {
  position: relative;
  top: -22px;
  font-size: 0.6em;
  font-weight: 600;
  color: rgb(25, 41, 101);
}
