@import 'styles/colorPalette.scss';

.root {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .container {
    .tableContainer {
      background: $color-4;
      outline: none;
      border: none;
      .tableHeaderCell {
        margin: 0;
        background: $color-2 !important;
        padding-top: 5px;
        .headerFields {
          // display: flex;
          align-items: center;
          .tableHeaderTitle {
            color: $color-12;
            margin-right: 8px;
            white-space: nowrap;
          }
          .tableHeaderSortIcon {
            &:hover {
              cursor: pointer;
            }
          }
        }
        .input {
          width: 100%;
        }
      }

      .tableButtonsGroup {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding: 20px;
      }
      .tableCell2 {
        background: $color-2 !important;
      }
      .tableBody {
        background: $color-1;

        .tableBodyRow {
          &:hover {
            cursor: pointer;
          }
        }
        .loadingComponent {
          width: 100%;
          height: 300px;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
    }
    .tableFooterButtonsGroup {
      margin-top: 20px;
      width: 100%;
      display: flex;
      justify-content: flex-end;
    }
  }
}
