@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";

.root {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    padding: 30px;
    height: auto;
    margin-bottom: 50px;
    .pageHeader {
        color: $color-12;
    }
}

.headerStyle {
    width: 100%;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: $color-9;
}

.cardTitleStyle {
    color: $color-10;
    margin-top: 0;
}
.headerCountStyle {
    color: $color-10;
    margin: 0;
}
