@import "../../Assets/colorPallete/colorPallete.scss";
@import "../../Assets/fontFamily/nunitoSans.scss";

.root {
  width: 100%;
  padding: 10px;
  .cardTitleStyle {
    color: $color-12;
    margin-top: 0;
  }
  .cardContainerStyle {
    display: flex;
    flex-direction: column;
    min-height: 500px;
    .headerStyle {
      width: 100%;
      padding: 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #dddfe3;
      .headerCountStyle {
        color: $color-9;
        margin: 0;
      }
      .headerTitleStyle {
        color: $color-12;
        margin: 0;
        font-weight: normal;
      }
    }
    .selectStyle {
      margin-bottom: auto;
      height: 100%;
      .selectValueStyle {
        color: $color-12;
        font-size: 0.9rem;
      }

      .addPartner {
        color: $color-9;
        position: absolute;
        transform: translate(-95%, 120%);
        font-size: 15px;
        font-weight: 500;
        text-transform: capitalize;

        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;

        &:hover {
          cursor: pointer;
        }
      }
    }
    .bodyStyle {
      width: 100%;
      .loadingComponent {
        width: 100%;
        height: 500px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      > div {
        > table {
          > thead > tr > th {
            padding: 5px 5px;
          }
          > tbody > tr > td {
            padding: 5px 5px;
          }
        }
      }
    }
    .footerStyle {
      padding: 20px;
      margin-top: auto;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      .footer {
        margin-left: auto;
        color: $color-9;
        font-weight: bold;
        text-decoration: none;
      }
    }
  }
}
