@import "styles/colorPalette.scss";

.addContact {
  font-size: 0.9vw;
  &:hover {
    cursor: pointer;
    font-weight: bolder;
  }
}

.root {
  width: 100%;
  height: 100%;
  display: grid;
  flex-direction: row;
  grid-template-rows: 1fr 1fr;
  color: $color-12;

  .firstInput,
  .secondInput {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;

    > div:nth-child(1) {
      width: 30%;
      margin-right: 2em;
    }
    > div:nth-child(2) {
      width: 30%;
      margin-right: 2em;
    }

    .div:nth-child(3) {
      width: 30%;
      margin-right: 2em;
    }
  }

  .firstInput1,
  .secondInput1 {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    padding-left: 2em;

    > div:nth-child(1) {
      width: 40%;
      margin-right: 1em;
      margin-left: 2em;
    }
    > div:nth-child(2) {
      width: 40%;
      margin-right: 1em;
      margin-left: 2em;
    }

    > div:nth-child(3) {
      display: none;
    }
  }
}
.DialogActions {
  width: 100%;
  height: auto;
  padding-right: 2em;

  .Cancel {
    background-color: $color-10;
    color: white;
    height: 80%;
    transform: translate(-55%, -10%);
    margin-right: 1em;
  }
  .editCancel {
    background-color: $color-10;
    color: white;
    height: 80%;
    transform: translate(-100%, -10%);
  }
  .Add {
    height: 80%;
    background-color: $color-9;
    color: white;
    padding-left: 2em;
    padding-right: 2em;
    margin-left: 1em;
    transform: translate(-50%, -10%);
  }
}
