@import "styles/colorPalette.scss";

.root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  display: grid;
  grid-template-rows: repeat(1fr, 8);
  grid-auto-rows: auto;

  > div {
    padding-left: 3em;
  }
  .textContainer {
    display: grid;
    flex-direction: rows;
    grid-template-rows: 1fr 0.2fr;
    color: $color-12;

    > div:nth-child(1) {
      font-weight: lighter;

      font-size: 20px;
      padding-bottom: 2em;
      padding-top: 1em;
      font-weight: 600;
      color: $color-4;
    }

    > div:nth-child(2) {
      font-size: 24px;

      font-weight: 500;
      padding: 0;
    }
  }

  .firstPartInputFields {
    display: grid;
    flex-direction: row;
    grid-template-rows: repeat(4, 1fr);
    grid-auto-rows: minmax(auto, 100%);
    padding-top: 1em;
    padding-bottom: 1em;
    grid-gap: 1em;
    color: $color-12;

    > div:nth-child(1) {
      display: flex;
      flex-direction: column;
      width: 61.5%;
    }

    .firstLayerFields {
      display: flex;
      flex-direction: row;
      > div {
        margin-right: 3.5em;
      }
      > div:nth-child(1) {
        width: 27%;
      }
      > div:nth-child(2) {
        width: 18%;

        > div {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          width: 100%;
          height: auto;

          > div:nth-child(1) {
            width: 45%;
            .customTag {
              color: $color-12;
              opacity: 0.5;
              font-size: 0.9vw;
              margin-left: 0.6em;
              margin-right: 0.6em;
            }
          }

          > div:nth-child(2) {
            width: 45%;
            display: flex;
            .customTag {
              color: $color-12;
              font-size: 0.9vw;
              opacity: 0.5;
              margin-left: 0.3em;
              margin-right: 0.3em;
            }
            > label {
              color: white;
            }
          }
        }
      }
    }

    .secondLayerFields {
      display: flex;
      flex-direction: row;
      > div {
        margin-right: 3.5em;
      }

      > div:nth-child(1) {
        width: 27%;
      }
      > div:nth-child(2) {
        width: 18%;
      }
      > div:nth-child(3) {
        width: 25.5%;
        overflow: visible;
        > label {
          overflow: visible;
        }
        > div {
          width: 50.5%;
          margin-top: 0.5em;
        }
      }
    }

    .thirdLayerFields {
      display: flex;
      flex-direction: row;
      > div {
        margin-right: 3.5em;
      }

      > div:nth-child(1) {
        width: 27%;
      }
      > div:nth-child(2) {
        width: 15.5%;
        > div {
          margin-top: 0.4em;
        }
      }
      > div:nth-child(3) {
        width: 22%;
        margin-top: 0.05em;
      }
    }
  }

  .radioButtonParts {
    display: flex;
    flex-direction: row;
    margin-top: 1.5em;
    color: $color-12;

    > div {
      margin-right: 6em;
    }
    > div:nth-child(1) {
      flex: 1 1 1;
      width: 25%;
      height: auto;
      color: $color-12;
      .label {
        font-weight: 550;

        color: $color-12;
      }
    }
    > div:nth-child(2) {
      flex: 1 1 1;
      width: 15%;
      margin-right: 3.7em;
      .label {
        font-weight: 550;

        color: $color-12;
      }
      .dropDown {
        width: 55%;
        margin-left: 2em;
      }
      .dropDownTinStatus {
        width: 70%;
        margin-left: 2em;
      }
    }
    > div:nth-child(3) {
      flex: 1 1 1;
      width: 25%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .TaxRate {
        width: auto;
        height: auto;
        padding-left: 3.3em;

        .customTag {
          color: $color-3;
          font-size: 20px;
          margin-left: 0.5em;
          margin-right: 0.5em;
          font-weight: lighter;
        }
        > div {
          width: 7vw;
        }

        .label {
          font-weight: 550;

          color: $color-12;
        }
      }

      > div:nth-child(2) {
        margin-left: 1em;
        width: 30%;
      }

      .label {
        font-weight: 550;

        color: $color-12;
      }
    }
  }

  .secondPartInputFields {
    display: grid;
    flex-direction: row;
    width: 100%;
    height: 100%;
    grid-template-rows: repeat(4, 1fr);
    grid-auto-rows: minmax(100px, auto);
    grid-gap: 0.5em;
    font-weight: 400;
    font-size: 15px;
    padding-top: 15px;
    padding-bottom: 10px;
    color: $color-12;

    .firstInputLayerFields {
      display: flex;
      flex-direction: row;
      width: 100%;
      height: 100px;

      > div:nth-child(1) {
        width: 18%;
        height: auto;
        margin-right: 3em;
      }

      > div:nth-child(2) {
        width: 25.1%;
        height: auto;
        margin-right: 3em;
      }
      > div:nth-child(3) {
        width: 22.3%;
        height: auto;
        margin-right: 3em;
      }
    }

    .secondInputLayerFields {
      display: flex;
      flex-direction: row;
      width: 100%;
      height: 100px;

      > div:nth-child(1) {
        width: 18%;
        height: auto;
        margin-right: 3em;
      }

      > div:nth-child(2) {
        width: 25.1%;
        height: auto;
        margin-right: 3em;
      }
      > div:nth-child(3) {
        width: 22.3%;
        height: auto;
        margin-right: 3em;
      }
    }

    .thirdInputLayerFields {
      display: flex;
      flex-direction: row;
      width: 100%;
      height: 100px;

      > div:nth-child(1) {
        width: 18%;
        height: auto;
        margin-right: 3em;
      }

      > div:nth-child(2) {
        width: 25.1%;
        height: auto;
        margin-right: 3em;
      }
      > div:nth-child(3) {
        width: 10%;
        height: auto;
        margin-right: 3em;
      }
    }

    .clinicScheduleAndRoomNumber {
      display: flex;
      flex-direction: row;
      width: 100%;
      height: 100px;

      > div:nth-child(1) {
        width: 46%;
        height: auto;
        margin-right: 3em;
      }

      > div:nth-child(2) {
        width: 10%;
        height: auto;
      }
    }
  }

  .contactInformationField {
    display: grid;
    grid-template-rows: 0.5fr 1.5fr;
    flex-direction: row;
    grid-auto-rows: minmax(auto, 100%);
    color: $color-12;

    > div:nth-child(1) {
      font-size: 24px;

      font-weight: 500;
      padding-top: 1%;
      padding-bottom: 1%;
    }
    .inputFields {
      display: flex;
      flex-direction: column;
      height: auto;
      width: 100%;

      > div {
        margin-right: 3.5em;
      }

      > div:nth-child(1) {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;

        > div:nth-child(1) {
          width: 26%;
          margin-right: 3.5em;
        }
        > div:nth-child(2) {
          width: 16.3%;
          margin-right: 3.5em;
        }
        > div:nth-child(3) {
          width: 22.3%;
          margin-right: 3.5em;
        }
      }

      > div:nth-child(2) {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;

        > div:nth-child(1) {
          width: 26%;
          margin-right: 3.5em;
        }

        > div:nth-child(2) {
          width: 16.3%;
          margin-right: 3.5em;
        }

        > div:nth-child(3) {
          width: 22.3%;
          margin-right: 3.5em;
          padding-top: 2.6em;
          user-select: none;
          > label {
            color: $color-9;
            &:hover {
              cursor: pointer;
              font-weight: bold;
            }
          }
        }
      }
    }
  }

  .contactInformationBoard {
    display: flex;
    flex-direction: column;
    padding-top: 1em;
    padding-bottom: 1em;
    justify-content: space-evenly;
    > div:nth-child(1) {
      font-size: 24px;

      font-weight: 500;
      letter-spacing: 1px;
      color: $color-12;
    }
    > div {
      flex: 1 1 1;
      margin-top: 0.5%;
      width: 60%;
      height: auto;

      .contactInformationTable {
        display: flex;
        flex-direction: column;
        background-color: $color-1;
      }
    }
  }

  .affiliateForm {
    display: flex;
    flex-direction: column;
    padding-top: 1em;
    padding-bottom: 1em;
    justify-content: space-evenly;

    > div:nth-child(1) {
      font-size: 24px;

      font-weight: 500;
      letter-spacing: 1px;
      color: $color-12;
    }
    > div {
      flex: 1 1 1;
      margin-top: 0.5%;
      width: 60%;
      height: auto;

      .affiliationInformationTable {
        display: flex;
        flex-direction: column;
        background-color: $color-2;
      }
    }
  }

  .remarksDiv {
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 1280px) {
  .root {
    .firstPartInputFields {
      > div:nth-child(1) {
        width: 63.5%;
      }
      .firstLayerFields {
        > div:nth-child(2) {
          > div {
            > div:nth-child(1) {
              .customTag {
                font-size: 11px;
              }
            }
            > div:nth-child(2) {
              .customTag {
                font-size: 11px;
              }
            }
          }
        }
      }
      .secondLayerFields {
        > div:nth-child(3) {
          width: 20.5%;
          > div {
            width: 150px;
            margin-top: 0.5em;
          }
        }
      }
    }

    .secondPartInputFields {
      .clinicScheduleAndRoomNumber {
        > div:nth-child(1) {
          width: 46.9%;
          height: auto;
          margin-right: 3em;
        }
        > div:nth-child(2) {
          width: 10%;
          height: auto;
        }
      }
    }
  }

  .radioButtonParts {
    > div:nth-child(3) {
      .TaxRate {
        transform: translateX(-10%);
      }
      > div:nth-child(2) {
        transform: translateX(-11%);
      }
    }
  }
}
