@import "styles/colorPalette.scss";

.root {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .buttonContainer {
        display: flex;
        justify-content: flex-end;
        .btnRed {
            margin: 0.5rem 0;
            background-color: $color-10;
            text-transform: none;
            color: $color-1;
        }
        .btnGreen {
            margin: 0.5rem 0;
            background-color: $color-5;
            text-transform: none;
            margin-right: 0.3rem;
            color: $color-1;
        }
        .btnAnchor {
            text-decoration: none;
        }
    }

    .container {
        margin: 2rem;
        background-color: white;
        width: 50vw;
        max-width: 70vw;
        height: 50vh;
        max-height: 80vh;
        overflow: scroll;
        outline: none;
        border: none;
        padding: 1rem;
        border-radius: 0.3rem;
    }
}
