.headerGroupContainer {
    display: flex;
    width: 100%;
    .doctorAssignmentContainer {
        .title {
            display: flex;
            align-items: center;
            > img {
                margin-right: 0.5em;
                width: 20px;
                height: 20px;
            }
            > label {
                font-size: 0.9vw;
                color: rgba(31, 58, 147, 0.5);
            }
        }
        .radioGroupContainer {
            .radioGroup {
                margin-left: 30px;
                display: flex;
                width: 100%;
                .none {
                    margin-right: 25px;
                    width: auto;
                }
                .assignedGroup {
                    .assigned {
                        width: auto;
                    }

                    .errorMessage{
                        font-size: 0.75rem;
                      //  padding-left: 1em;
                        color: red;
                    }
                    .checkBoxGroup {
                        margin-left: 0.1em;

                        .specialAccommodation {
                            display: flex;
                            width: 100%;
                            flex-direction: row;
                            align-items: center;
                            white-space: nowrap;
                            .inputHide {
                                display: none;
                            }
                            .exclusiveTo {
                                position: absolute;
                                margin-left: 230px;
                                display: flex;
                                flex-direction: row;
                                align-items: center;
                                width: 100%;
                                .inputField {
                                    margin-left: 5px;
                                }
                                > label {
                                    font-size: 15px;
                                    font-weight: 595;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .medicalPartnerStatusContainer {
        margin-left: 5em;
        width: auto;
        .title {
            display: flex;
            align-items: center;
            > label {
                font-size: 0.9vw;
                color: rgba(31, 58, 147, 0.5);
            }
        }
        .radioGroupContainer {
            display: flex;
            width: 100%;
            height: auto;

            .radioGroup {
                .onHold {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    width: 100%;
                    .onHoldInput {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        .inputField {
                            margin-left: 5px;
                        }
                        > label {
                            font-size: 15px;
                            font-weight: 595;
                        }
                    }
                    .onHoldHide {
                        display: none;
                    }
                }
            }
        }
    }
}
