@import "styles/colorPalette.scss";

.listStyles {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    margin-bottom: 1.5em;
    text-transform: uppercase;

    .firstRow {
        width: 50%;
        height: auto;
        display: flex;
        flex-direction: row;

        > label:nth-child(1) {
            font-size: 0.9vw;

            width: 45%;
            height: auto;
            color: rgba(31, 58, 147, 0.5);
        }

        > label:nth-child(2) {
            font-size: 0.9vw;

            width: 50%;
            height: auto;
            color: $color-12;
        }
    }

    .secondRow {
        width: 50%;
        height: auto;
        display: flex;
        flex-direction: row;

        > label:nth-child(1) {
            font-size: 0.9vw;

            width: 32%;

            height: auto;
            color: rgba(31, 58, 147, 0.5);
        }

        > label:nth-child(2) {
            font-size: 0.9vw;

            width: 60%;
            height: auto;
            color: $color-12;
        }
    }
}

