.listStyles:nth-child(4),
.listStyles:nth-child(5) {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    margin-bottom: 1.5em;
    text-transform: uppercase;

    .firstRow {
        min-width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;

        > label:nth-child(1) {
            font-size: 0.9vw;

            width: 28.8%;
            height: auto;
            color: rgba(31, 58, 147, 0.5);
        }

        > label:nth-child(2) {
            font-size: 0.9vw;

            width: 100%;
            height: auto;
            color: $color-12;
        }
    }
}

.loader{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}
