@import "styles/colorPalette.scss";

.rightBody {
  width: 100%;
}

.textField {
  width: 145px;
  height: 100%;
  padding: 0;
}

.previewButton {
  text-align: right;
}

.DownloadPreviewBtn {
  margin-top: 15px !important;
  background-color: $color-11 !important;
  color: #fff !important;
}

.DownloadPreviewBtnMedical {
  margin-top: 10px !important;
}

.Pagination {
  margin-top: 10px;
  text-align: right;
}

.previewTable {
  border: 1px solid gray;
  width: 100%;

  td {
    border: 1px solid gray;
  }
}

.rootTableWraper {
 // overflow-x: auto;
  min-height: 450px;
}

.rootTable {
  height: auto;

  > thead > tr > th {
    padding: 10px;
  }
  
  > tbody > tr > th {
    padding: 10px;
    word-break: break-word;
  }

  .tableHead {
    background-color: $color-2;
    -webkit-user-select: none; /* Chrome all / Safari all */
    -moz-user-select: none; /* Firefox all */
    -ms-user-select: none; /* IE 10+ */
    user-select: none;

    .withTextfield {
      min-width: 140px;
    }

    .noTextField {
      min-width: 140px;
      margin-bottom: 65px;
    }

    .showIcon {
      display: inline-block;
      padding-left: 5px;
    }

    .tableHeadRow {
      color: $color-3;
      height: auto;
    }
  }

  .tableBody {
    width: 100%;
    height: auto;

    .tableRow {
      width: 100%;
      height: auto;

      .tableCell {
        font-size: 0.9vw;

        input {
          width: 100px;
        }
      }
    }

    .totalFooter {
      background-color: $color-13;
      .tableCell {
        color: white;
        font-size: 0.9vw;
      }
    }

    .tableRow:nth-of-type(even) {
      background-color: white;
    }

    .tableRow:nth-of-type(odd) {
      background-color: $color-1;
    }
  }
}

.DialogActions {
  width: 100%;
  height: auto;
  padding-right: 2em;

  .Cancel {
    background-color: $color-10;
    color: white;
    height: 80%;
    transform: translate(-20%, -10%);
    padding-left: 2em;
    padding-right: 2em;
    text-transform: initial;
  }

  .Add {
    height: 80%;
    background-color: $color-8;
    color: white;
    padding-left: 2em;
    padding-right: 2em;
    transform: translate(-15%, -10%);
    text-transform: initial;
  }

  .disablePreviewButton {
    height: 80%;
    // background-color: $color-8;
    // color: white;
    padding-left: 2em;
    padding-right: 2em;
    transform: translate(-15%, -10%);
    text-transform: initial;
  }
}
