@import "styles/colorPalette.scss";

.FooterButtonContainer {
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  bottom: 0;
  height: auto;
  padding: 0;
  z-index: 1;

  .FooterButtonWrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-top: 2%;
    width: 100%;
    height: auto;
    background-color: $color-4;
    padding-left: 3em;

    > div:nth-child(1) {
      padding-top: 1.5em;
      padding-bottom: 1.5em;
      .cancelButton {
        padding-top: 0.75em;
        padding-bottom: 0.75em;
        background-color: $color-10;
        padding-left: 1.5em;
        padding-right: 1.5em;
      }
    }
    > div:nth-child(2) {
      flex: 1;
    }
    > div:nth-child(3) {
      display: flex;
      justify-content: space-evenly;
      padding-top: 1.5em;
      padding-bottom: 1.5em;
      flex-direction: row;
      margin-right: 10em;

      > div:nth-child(1) {
        display: flex;

        .SaveButton {
          display: grid;
          flex-direction: row;
          padding-right: 2em;
          padding-left: 2em;
          background-color: $color-9;
          color: white;
          text-transform: capitalize;
        }
        .disabledButton {
          display: grid;
          flex-direction: row;
          padding-right: 1.5em;
          padding-left: 1.5em;
          background-color: $color-2;
          color: white;
          text-transform: capitalize;
        }
      }

      > div:nth-child(2) {
        margin-left: 1em;
        .ProceedButton {
          padding-top: 0.75em;
          padding-bottom: 0.75em;
          padding-right: 1.5em;
          padding-left: 1.5em;
          background-color: $color-9;
          color: white;
          text-transform: capitalize;
        }
        .backgroundChange {
          background-color: $color-2;
          padding-top: 0.75em;
          padding-bottom: 0.75em;
          padding-right: 1.5em;
          padding-left: 1.5em;
          color: white;
          text-transform: capitalize;
        }
      }
    }
  }
}
