.otherServiceForm {
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  display: grid;
  grid-template-rows: repeat(1fr, 8);
  grid-auto-rows: auto;

  > div {
    padding-left: 3em;
  }

  .firstPartInputFields {
    display: grid;
    flex-direction: row;
    grid-template-rows: repeat(2, 1fr);
    grid-auto-rows: minmax(auto, 100%);
    font-weight: 400;
    font-size: 15px;
    padding-top: 1.5em;
    padding-bottom: 1em;
    grid-gap: 1em;
    color: darkblue;

    > div:nth-child(1) {
      display: flex;
      flex-direction: column;
      width: 17%;
    }

    > div:nth-child(2) {
      display: flex;
      flex-direction: column;
      width: 43%;
    }
  }

  .SecondPartInputFields {
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.5fr 0.6fr 1.4fr;
    grid-gap: 1em;
    font-weight: 400;
    font-size: 15px;
    padding-top: 5px;
    padding-bottom: 15px;
    color: darkblue;

    > div:nth-child(1) {
      > div:nth-child(1) {
        margin-bottom: 3%;
      }
      > div:nth-child(2) {
        margin-bottom: 3%;
      }
      > div:nth-child(3) {
        margin-bottom: 15%;
      }
    }
    > div:nth-child(2) {
      margin-right: 2em;
      margin-left: 2em;

      > div:nth-child(1) {
        margin-bottom: 3%;
      }
      > div:nth-child(2) {
        margin-bottom: 3%;
      }
    }

    > div:nth-child(3) {
      > div:nth-child(1) {
        margin-bottom: 1%;
        width: 41%;
      }
      > div:nth-child(2) {
        margin-bottom: 1%;
        width: 41%;
      }
      > div:nth-child(3) {
        width: 19%;
      }
    }
  }

  .thirdPartInputFields {
    display: grid;
    flex-direction: row;
    grid-template-rows: repeat(2, 1fr);
    grid-auto-rows: minmax(auto, 100%);
    font-weight: 400;
    font-size: 15px;
    // padding-top: 1em;
    padding-bottom: 1em;
    grid-gap: 1em;
    color: darkblue;

    > div:nth-child(1) {
      display: flex;
      flex-direction: column;
      width: 25%;
    }
    > div:nth-child(2) {
      display: flex;
      flex-direction: column;
      width: 25%;
    }
    > div:nth-child(3) {
      display: flex;
      flex-direction: column;
      width: 25%;
    }
  }
}
