@import "styles/colorPalette.scss";

.root {
  width: 100%;
  height: 100%;
  color: $color-12;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  .listStyles {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    margin-bottom: 1.5em;
    text-transform: uppercase;

    .firstRow {
      width: 45%;
      height: auto;
      display: flex;
      flex-direction: row;

      > label:nth-child(1) {
        font-size: 0.9vw;

        width: 36%;
        height: auto;
        color: rgba(31, 58, 147, 0.5);
      }

      > label:nth-child(2) {
        font-size: 0.9vw;

        width: 80%;
        height: auto;
        color: $color-12;
      }
    }

    .secondRow {
      margin-left: 2em;
      width: 50%;
      height: auto;
      display: flex;
      flex-direction: row;

      > label:nth-child(1) {
        font-size: 0.9vw;

        width: 36%;

        height: auto;
        color: rgba(31, 58, 147, 0.5);
      }

      > label:nth-child(2) {
        font-size: 0.9vw;

        width: 80%;
        height: auto;
        color: $color-12;
      }
    }
  }

  .listStyles:nth-child(15) {
    width: 100%;

    .firstRow {
      width: 1800%;
      height: auto;
      display: flex;
      flex-direction: row;

      > label:nth-child(1) {
        font-size: 0.9vw;

        width: 34.5%;
        height: auto;
        color: rgba(31, 58, 147, 0.5);
      }

      > label:nth-child(2) {
        font-size: 0.9vw;

        width: 200%;
        height: auto;
        color: $color-12;
      }
    }
  }
}

.changeName {
  display: flex;
  flex-direction: row;
  min-width: 100%;
  height: auto;
  color: $color-12;
  font-size: 23px;

  .TextField {
    width: 100%;
    height: auto;
  }

  .editIcon {
    display: flex;
    margin-right: 0.5em;
  }
}
