@import 'styles/colorPalette.scss';

.root {
  .headerContainer {
    position: relative;
    width: 100%;
    height: auto;
    display: grid;
    padding: 0;
    grid-template-rows: 0.3fr 0.5fr;
    grid-auto-rows: minmax(100%, auto);
    z-index: 1;
    color: $color-12;

    .breadCrumbs {
      height: auto;
      padding-top: 20px;
      padding-bottom: 20px;
      padding-left: 2.5em;

      .breadCrumblistColor {
        color: $color-12 !important;
        text-decoration: none;
      }
      .lastTextColor {
        font-weight: bold;
        color: $color-12 !important;
        text-decoration: none;
      }
    }

    .secondContainer {
      width: 100%;
      height: auto;
      display: flex;
      padding-left: 2.5em;
      flex-direction: row;
      justify-content: space-evenly;
      color: $color-12;
      position: relative;

      

      .headerTitle {
        display: flex;
        flex-direction: row;
        width: 85%;
        color: $color-12;
        padding-top: 0.5em;
        padding-bottom: 0.2em;
        font-size: 23px;
        letter-spacing: 0.1em;

        

        .nameTitle {
          // max-width: 75%;
          // min-width: 75%;
          word-wrap: break-word;
          height: auto;
          font-weight: bold;
        }

        .editTitle {
          // margin-top: 0.4em;
          // width: auto;
          // height: auto;
          max-width: 85%;
          // min-width: 75%;
          // word-break: break-all;
          font-weight: bold;

        }

        > label {
          margin-top: 1.1em;
          font-size: 12px;

          letter-spacing: 0.1em;
        }
        .indicator {
          // margin-top: 0.6em;
          // margin-left: 0.5em;
          // margin-right: 0.2em;
          margin: 4px;
          border-radius: 80%;
          width: 12px;
          height: 12px;
          border: 1px solid $color-9;
          background-color: $color-9;
          box-shadow: $color-9 0 -1px 1px 1px, inset $color-9 0 -1px 9px, $color-9 0 1px 12px;

          &.indicator1 {
            border: 1px solid $color-10;
            background-color: $color-10;
            box-shadow: $color-10 0 -1px 1px 1px, inset $color-10 0 -1px 9px, $color-10 0 1px 12px;
          }

          &.indicator2 {
            border: 1px solid $color-6;
            background-color: $color-6;
            box-shadow: $color-6 0 -1px 1px 1px, inset $color-6 0 -1px 9px, $color-6 0 1px 12px;
          }

          &.indicator3 {
            border: 1px solid $color-9;
            background-color: $color-9;
            box-shadow: $color-9 0 -1px 1px 1px, inset $color-9 0 -1px 9px, $color-9 0 1px 12px;
          }
        }
      }
      > div:nth-child(2) {
        flex: 1;
      }
    }
  }
}
.logoStyle {
  padding-left: 1.5em;
  flex: 1;

  img {
    max-height: 30px;
  }
}

.flag-icon {
  color: red;
  position: relative;
  bottom: 10px;
  font-size: 16px;
}

.edit-details-btn {
  position: absolute;
  right: 30px;
  bottom: 10px;
  max-height: 30px;
  font-size: 0.4em;
  background-color: #3ab77d;
  color: #fff;
  font-weight: 800;
}
.textIndicator {
  display: flex;
  flex-direction: row;
  font-size: small; 
  padding-top: 5px;
  margin-left: 5px;
}