.root {
    display: flex;
    width: 100%;
    flex-direction: column;
  
    .label {
      padding-bottom: 5px;
    }
  
    .wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        position: relative;
        margin-bottom: 1em;
      .read-only-value {
        font-weight: unset;
        padding: 5px;
      }
  
      .select-container {
        flex: 1;
        display: flex;
  
        .select {
          padding: 0;
        }
  
        .input {
          width: 100%;
          .select {
            padding: 10px;
            padding-right: 20px;
            text-align: left;
          }
        }
  
        // Errors
        &.has-error {
          border: 1px solid red;
          margin-bottom: unset;
        }
  
        &.disabled {
          background-color: rgb(223, 223, 223);
        }
      }
      .errorField{
          width: 100%;
          height: auto;
          display: flex;
        
        text-align: center;

        span.error {
           position: absolute;
           color: red;
           width: 100%;
           font-size: 11px !important;
           overflow-wrap: break-word;
         }
      }
     
    }
  }
  