@import "styles/colorPalette.scss";

.integerComponent {
    display: flex;
    width: 100%;
    input {
        padding: 10px;
        text-align: center;
    }
}

.priceComponent {
    display: flex;
    width: 100%;
    input {
        padding: 10px;
        text-align: center;
    }
}

.multiTextComponent {
    display: flex;
    width: 100%;
    padding: 0 !important;
    textarea {
        padding: 10px;
        text-align: left;
    }
}

.circularButton {
    background-color: $color-1;
    border: solid 2px $color-4;
    border-radius: 50%;
    height: 67px;
    width: 67px;
    margin: 0 5px;
    &:hover {
        cursor: pointer;
    }
    &:focus {
        outline: none;
    }
    .circularButtonContent {
        display: flex;
        justify-content: center;
        align-items: center;
        border: none;
    }
}

.tableCell {
    color: $color-12;
    .tableCellTitle {
        color: $color-9;
        font-weight: bold;
        margin: 0;
    }
    .tableCellText {
        margin: 0;
        color: $color-12;
        font-weight: normal;
        font-size: 0.9rem;
    }
}
