@import "styles/colorPalette.scss";

.tableContainer {
  width: 100%;
  height: auto;
  background-color: white;
  margin-bottom: 1em;

  .table {
    color: $color-12;

    .table-row {
      .description-row {
        color: $color-12;
        font-size: 0.9vw;
        width: 100%;
      }

      .DescriptionRowWithError {
        color: $color-12;
        font-size: 0.9vw;
      }
    }

    .table-row:hover {
      opacity: 0.5;
      cursor: pointer;
    }

    .table-row:nth-of-type(even) {
      background-color: $color-1;
    }
    .table-row:nth-of-type(odd) {
      background-color: white;
    }
  }
}

.buttonContainer {
  width: auto;
  height: auto;
  display: flex;
  justify-content: flex-end;

  .buttonStyle {
    // display: flex;
    justify-content: center;
    align-items: center;
    border: 0.5px solid $color-1;
    width: 1vw;
    // height: 1vh;
    padding: 1.3em;
    color: $color-9;
    background-color: white;
    font-weight: bold;

    border-radius: 5px;
    font-size: 0.6vw;
    // margin-top: -20px;
  }
}
.WithSubScript {
  width: 100%;
  height: auto;
  .subscript {
    font-size: 0.6vw;
    color: $color-12;
    opacity: 0.8;
    padding-top: 10em;
    margin-left: 0.5em;
  }
}

.WithAndErrorSubScript {
  width: 50%;
  padding: 5px;
  height: auto;
  .subscript {
    font-size: 0.6vw;
    color: $color-12;
    opacity: 0.8;
    padding-top: 10em;
    margin-left: 0.5em;
  }
}

.validationStyle {
  border: 1px solid $color-10 !important;
  width: 55% !important;
  padding: 5px !important;
}
