@import "styles/color.scss";
.root {
  width: 100%;
  height: 100%;
  color: $color-25;

  .textAreaContainer {
    width: 100%;
    height: 100%;
    padding: 0;
    display: flex;

    > textarea {
      resize: none;
      width: 100%;
      height: 100%;
    }
  }

  .withScroll {
    width: 100%;
    height: 100%;
    padding: 0;
    display: flex;

    > textarea {
      resize: none;
      width: 100%;
      height: 100%;
      overflow-y: scroll;
    }
  }
}
