@import "styles/colorPalette.scss";

.tableContainer {
  width: 100%;
  height: 100%;
  padding: 0.2em;
  background-color: $color-2;
  overflow: hidden;

  .table {
    overflow: hidden;

    .TableBody {
      width: 100%;
      height: 100%;
      font-size: 12px;
      .inlineNormal {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;

        .inlineTableCell {
          width: 100%;
          height: auto;
        }
      }
      .editAndDelete {
        width: 10%;
        height: 5%;
        display: flex;
        flex-direction: row;
      }
      .editAndDelete:hover {
        cursor: pointer;
      }

      .tableRowEdit {
        widows: 100%;
        height: auto;
        display: flex;
        flex-direction: row;
        justify-content: center;
        text-align: center;

        .tableCellEdit {
          width: 100%;
          height: auto;
          display: flex;

          .textEdit {
            width: 90%;
            height: auto;
            display: flex;
            font-size: 15px;
            text-align: center;
          }
        }
      }
    }
  }

  .InputConatiner {
    display: flex;
    flex-direction: row;

    height: auto;
    width: 100%;

    .InputFieldHeader {
      width: 85%;
      margin-right: 1.5em;
    }

    .AddTolist {
      padding-top: 1%;
      text-decoration: underline;
      color: $color-9;
    }
  }
}


