.pagination {
  width: 97%;
  height: auto;
  display: flex;
  margin-top: 1em;
  margin-left: 1.5%;
  margin-right: 1.5%;
  margin-bottom: 2em;

  .tableLabel {
    flex: 1;
  }

  .options {
    flex: 2;
    display: flex;
    justify-content: flex-end;
    width: 100%;
    height: auto;

    .showText {
      transform: translateY(-12%);
      margin-right: 1em;
      height: auto;
    }
  }
}
