@import "styles/colorPalette.scss";

.bodyContainer {
  width: 100%;
  height: 100%;
  display: grid;
  flex-direction: column;
  grid-auto-rows: minmax(100%, auto);
  grid-template-rows: 0.2fr 2.2fr;

  > div {
    padding: 0;
  }

  .removeTitleText {
    > text {
      display: none;
    }
  }
  .text-title {
    font-weight: lighter;
    font-family: Georgia, "Times New Roman", Times, serif;
    padding-top: 0.5em;
    padding-left: 3em;
    color: $color-4;
    height: 70px;
  }
  .tableContainer {
    width: 100%;
    height: 100%;
    padding-left: 3em;
    display: grid;
    flex-direction: column;
    grid-template-columns: 1.2fr 0.8fr;
    grid-auto-rows: minmax(100%, auto);
    grid-gap: 1em;

    .uploadFileTable {
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-rows: 0.1fr 1.5fr;
      grid-auto-rows: minmax(100%, auto);
      flex-direction: row;

      .tableTopText {
        font-size: 12px;
        color: $color-12;
        height: 30px;
      }
      .removeDescription {
        > text {
          display: none;
        }
      }
      .tableBox {
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-rows: 0.2fr 1.8fr;
        grid-auto-rows: minmax(100%, auto);
        padding-left: 0.3em;
        padding-right: 0.3em;
        padding-bottom: 1.4em;
        background-color: $color-2;

        .documentDesriptionText {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          padding: 1em;
          height: 75px;
          color: $color-3;
          font-size: 15px;
          width: 100%;
        }
        .uploadingOfDocuments {
          width: 100%;
          height: auto;
        }
      }
    }

    .viewFileDrawer {
      padding: 1em;

      .drawerContainer {
        margin-top: 1.2em;
        width: 90%;
        height: 70%;
      }
    }
  }

  .buttonContainer {
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    bottom: 0;
    z-index: 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding-top: 1.8em;

    .buttonDiv {
      display: flex;
      flex-direction: row;
      width: 100%;
      height: 80px;
      background-color: $color-4;
      justify-content: flex-end;
      align-items: center;

      .buttonBox {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        width: auto;
        height: 60%;
        flex: 0.5em, 0.5em, 1em;
        margin-right: 7em;

        > div:nth-child(1) {
          display: flex;
          padding-right: 3em;
          .ReturnButton {
            display: grid;
            flex-direction: row;
            padding-right: 1.5em;
            padding-left: 1.5em;
            background-color: $color-9;
            color: white;
          }
        }

        > div:nth-child(2) {
          display: flex;
          padding-right: 2.5em;
          .SaveButton {
            display: grid;
            flex-direction: row;
            padding-right: 1.5em;
            padding-left: 1.5em;
            background-color: $color-9;
            color: white;
          }
        }

        > div:nth-child(3) {
          display: flex;
        }
      }
    }
  }
  .Footer {
    display: flex;
    justify-content: flex-end;
    padding-top: 2em;
    padding-right: 3em;
    color: $color-3;
    height: 60px;
    font-weight: bolder;
  }
}
