@import 'styles/colorPalette.scss';

._firstLayerFields {
  display: flex;
  flex-direction: row;
  width: 100%;

  > div:nth-child(1) {
    width: 60%;
  }

  > div:nth-child(2) {
    width: 40%;
    display: flex;
    flex-direction: row;
    // margin-bottom: 5px;
    margin-left: 20px;

    button {
      margin-top: 37px;
      height: 10px;
      color: green;
      border: none;
      background: none;
      text-decoration: underline;
      cursor: pointer;
      padding: 0;
    }
  }
}

.GeneralInformationFieldN {
  width: 100%;
  padding-left: 2.5em;
  height: auto;
  font-weight: 500;
  color: $color-12;

  .generalInfoTextN {
    width: 100%;
    height: 50px;
    color: $color-12;
    font-weight: 400;
    font-size: 24px;
  }

  .firstRow {
    width: 81%;
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.5fr 0.5fr 0.5fr;
    grid-gap: 3em;
    padding-top: 5px;
  }

  .secondRow {
    width: 53%;
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.5fr 0.5fr;
    grid-gap: 3em;

    .secondRowLeft {
      display: grid;
      flex-direction: column;
      grid-template-columns: 0.35fr 0.65fr;
      grid-gap: 3em;
    }

    .secondRowRight {
      display: grid;
      flex-direction: column;
      grid-template-columns: 0.5fr 0.5fr;
      grid-gap: 3em;
    }
  }

  .thirdRow {
    width: 53%;
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.5fr 0.5fr;
    grid-gap: 3em;
  }

  .fourthRow {
    width: 56%;
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.5fr 0.5fr;
  }

  .fifthRow {
    width: 53%;
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.5fr 0.5fr;
    grid-gap: 3em;
  }

  .sixthRow {
    width: 81%;
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.33fr 0.33fr 0.33fr;
    grid-gap: 3em;
  }

  .seventhRow {
    width: 80%;
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.34fr 0.22fr 0.24fr 0.2fr;
    grid-gap: 3em;
  }

  .eighthRow {
    width: 80%;
    display: flex;
    flex-direction: row;
    grid-gap: 3em;

    > div:not(:last-child) {
      width: 30%;
    }

    > div:last-child {
      align-self: center;
      user-select: none;
    }
    .check-payee-label {
      > span:last-child {
        font-size: 10px;
        cursor: default;
      }
    }
    .dropdown-icon-wrapper {
      width: 10% !important;
    }
    
    .dropdown-wrapper {
      position: absolute;
      top: 70%;
      background-color: white;
      z-index: 1;
      width: 100%;
      border-radius: 4px;
      > div {
        padding: 10px;
        cursor: pointer;
        &:hover {
          background-color: #dfdfdf;
        }
        &:not(:last-child) {
          margin-bottom: 5px;
        }
      }
    }
  }
}

.GeneralInformationField {
  display: grid;
  width: 100%;
  padding-left: 2.5em;
  height: auto;
  grid-template-rows: 0.2fr 1.8fr;
  flex-direction: row;
  grid-auto-rows: minmax(100%, auto);

  font-weight: 500;
  color: $color-12;

  .generalInfoText {
    width: 100%;
    height: 50px;
    color: $color-12;
    font-weight: 400;
    font-size: 24px;
  }

  .generalInfoFields {
    display: grid;
    grid-template-columns: 0.4fr 1fr;
    grid-auto-rows: minmax(100%, auto);
    flex-direction: column;

    .firstPartField {
      display: flex;
      flex-direction: column;
      width: 100%;

      .InputContainer {
        width: 90%;
      }

      .InputContainerTwo {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        width: 90%;

        .suffix {
          width: 35%;
          height: auto;
          margin-right: 7%;
        }
        .birthDate {
          width: 65%;
          height: auto;

          > label {
            margin-bottom: 1em;
          }
          > div {
            margin-top: 0.35em;
            width: 100%;
            height: auto;
          }
        }
      }

      .InputContainerDropDown {
        margin-bottom: 1.6em;
        width: 90%;
      }
    }

    .secondPartField {
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;

      .textField {
        width: 35%;
      }
      .DropDowntextField {
        width: 35%;
        margin-bottom: 1.65em;
      }

      .firstTextField {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: auto;

        .middleName {
          width: 35%;
          height: 100%;
          margin-right: 2.5em;
        }
        .lastName {
          width: 35%;
          height: 100%;
        }
      }

      .secondTextField {
        display: flex;
        flex-direction: row;
        // margin-bottom: 1.6em;

        .civilStatus {
          width: 16%;
          margin-right: 3%;
        }
        .gender {
          width: 16%;
        }
      }

      .ptrNumberAndS2Number {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: auto;

        > div:nth-child(1) {
          width: 35%;
          height: 100%;
          margin-right: 2.5em;
        }

        > div:nth-child(2) {
          width: 35%;
          height: 100%;
        }
      }

      .textFieldforPRCfield {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: auto;
        padding: 0;
        color: $color-12;

        > div:nth-child(1) {
          width: 21.5%;
          margin-right: 2.1em;
          margin-top: 0.3em;
          height: auto;
        }
        > div:nth-child(2) {
          width: 25.5%;
          margin-right: 2em;
        }
        > div:nth-child(3) {
          width: 20.1%;
          margin-top: 0.3em;
        }
      }
    }
  }
}
