@import 'styles/colorPalette.scss';

.medicalPartnerID {
  width: 100%;
  height: 100%;
  padding-left: 2.5em;
  padding-bottom: 0;
  font-size: 16px;

  color: $color-12;

  display: flex;
  flex-direction: row;
  > div:nth-child(2) {
    width: 60%;
    display: flex;
    flex-direction: row;
    margin-bottom: 15px;
    margin-left: 20px;

    button {
      margin-top: 40px;
      height: 10px;
      color: green;
      border: none;
      background: none;
      text-decoration: underline;
      cursor: pointer;
      padding: 0;
    }
  }
  .medicalPartnerIdContainer {
    width: 20%;
    height: auto;
  }
}
