@import '../../../Assets/colorPallete/colorPallete.scss';
@import '../../../Assets/fontFamily/nunitoSans.scss';
.root {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: calc(100vh - 180px);

  .content {
    padding: 20px;
    height: 100%;

    .buttonContainer {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      height: auto;

      .button {
        background-color: $color-9;
        color: $color-1;
      }

      .action {
        background-color: $color-9;
        color: $color-1;
      }
    }
  }
}
