@import "styles/colorPalette.scss";
.root {
    .title {
        color: #1e2071;
        margin-bottom: 0;
    }
    .content {
        width: 100%;
        padding: 20px;
        padding-top: 0;
        .componentRadioGroup {
            display: flex;
            flex-direction: column;
            .row {
                display: flex;
                align-items: center;
                .radioFieldContainer {
                    width: 220px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    .componentRadioLabel {
                        &Disable {
                            color: #9ea3ac;
                            &:hover {
                                cursor: pointer;
                            }
                        }
                        color: #1e2071;
                        font-weight: bold;
                        &:hover {
                            cursor: pointer;
                        }
                    }
                    .componentRadioButton {
                        color: $color-12;
                    }
                }
                .inputFieldContainer {
                    display: flex;
                    width: 560px;
                    justify-content: space-between;
                    align-items: center;
                    .componentRadioInputLabel {
                        color: #1e2071;
                        font-weight: normal;
                        &Disable {
                            font-weight: normal;
                            color: #9ea3ac;
                            &:hover {
                                cursor: pointer;
                            }
                        }
                    }
                }
                .readOnlyInputFieldContainer {
                    display: flex;
                    align-items: center;
                    .componentRadioInputLabel {
                        color: #1e2071;
                        font-weight: normal;
                        &Disable {
                            font-weight: normal;
                            color: #9ea3ac;
                            &:hover {
                                cursor: pointer;
                            }
                        }
                    }
                }
            }
        }
    }
}
