.root {
  position: relative;
  background: #525659;
  min-height: 100%;

  .controls {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px;
    background-color: #323639;
    position: -webkit-sticky;
    position: sticky;
    top: -21px;
    z-index: 999;

    .control {
      span {
        color: white;
        font-size: 17px;
      }
    }
  }

  .pdfView {
    position: inherit;
  }

  .pdf {
    display: flex;
    flex: 1;
    justify-content: center;
    padding: 10px;
    padding-top: 70px;
  }
  .page-number {
    color: #dddddd;
    font-weight: 600;
  }
  .file-name {
    color: #dddddd;
    font-weight: 600;
    margin-right: 50px;
  }
}

.react-pdf__Page {
  position: inherit !important;
}
