@import "styles/colorPalette.scss";


.radioButtonParts {
  display: flex;
  flex-direction: row;
  margin-top: 1.5em;
  color: $color-12;

  > div {
    margin-right: 6em;
  }
  > div:nth-child(1) {
    flex: 1 1 1;
    width: 24%;
    height: auto;
    color: $color-12;
    .label {
      font-weight: 550;

      color: $color-12;
    }
  }
  > div:nth-child(2) {
    // flex: 1 1 1;
    width: 25%;
    // margin-right: 3.7em;
    .label {
      font-weight: 550;

      color: $color-12;
    }
    .dropDown {
      width: 77%;
      margin-left: 2em;
    }
  }
  > div:nth-child(3) {
    flex: 1 1 1;
    width: 25%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .TaxRate {
      width: auto;
      height: auto;
      padding-left: 3.3em;

      .customTag {
        color: $color-3;
        font-size: 20px;
        margin-left: 0.5em;
        margin-right: 0.5em;
        font-weight: lighter;
      }
      > div {
        width: 7vw;
      }

      .label {
        font-weight: 550;

        color: $color-12;
      }
    }

    > div:nth-child(2) {
      margin-left: 1em;
      width: 30%;
    }

    .label {
      font-weight: 550;

      color: $color-12;
    }
  }
}
