@import "../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../Assets/fontFamily/nunitoSans.scss";

.root {
  position: absolute;
  left: 0;
  top: 0%;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;

  > div {
    padding: 0;
  }

  > div:nth-child(1) {
    position: sticky;
  }

  .contentWrapper {
    width: 100%;
    height: 100%;

    .adjustDrawer {
      margin-top: 2em;
      width: 90%;
      height: 70%;
    }
  }
}

@media only screen and (max-width: 1280px) {
  .root {
    .contentWrapper {
      .adjustDrawer {
        margin-top: 1.1em;
        width: 90%;
        height: 70%;
      }
    }
  }
}
