@import "styles/colorPalette.scss";


.cancelButton {
  padding-top: 0.75em;
  padding-bottom: 0.75em;
  background-color: $color-10;
  padding-left: 1.5em;
  padding-right: 1.5em;
  text-transform: capitalize;
}

.dialogContainer {
  .btnGroup {
      display: flex;
      justify-content: center;
      .btnCancel {
          background: $color-10;
          color: $color-1;
          margin: 0 10px;
          text-transform: none;
      }
      .btnSave {
          background: $color-9;
          color: $color-1;
          margin: 0 10px;
          text-transform: none;
      }
  }
}
