@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";

.root {
    width: 95%;
    margin: 5% auto 0 auto;
    .loadingComponent {
        width: 100%;
        height: 35%;
        z-index: 99999;
        position: absolute;
        transform: translate(0%, 0%);
        text-align: center;
        padding-top: 20%;
    }

    .tableWrapper {
        background: $color-4;
        padding: 5em 0 0 0;
        // margin-left: 5.8em;
        .table {
            width: 100%;
            height: auto;
            background: white;
            .tableHeader {
                background: $color-2 !important;
                width: 100%;
                height: auto;
                .tableHeaderCell {
                    margin: 0;
                    background: $color-2 !important;
                    .tableHeaderTitle {
                        color: $color-12;
                        margin-right: 8px;
                    }
                    .tableHeaderSortIcon {
                        &:hover {
                            cursor: pointer;
                        }
                    }
                    .tableHeaderAutoComplete {
                        width: 100%;
                        background: #f5f7fb;
                    }
                }
            }
    
            .tableBody {
                background: $color-1;
                .tableBodyRow {
                    &:hover {
                        cursor: pointer;
                    }
                }
            }
        }
        .tablePagination {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin: 10px;
            .options {
                display: flex;
                align-items: center;
            }
        }

    }
    
    .footer {
        display: flex;
        width: 100%;
        justify-content: flex-end;
        .btnRed {
            background: $color-10;
            margin-top: 10px;
            color: $color-1;
        }
    }
}

.tableRowStyle {
    &:hover {
        cursor: pointer;
    }
}
