@import "styles/colorPalette.scss";

.root {
    width: 100%;
    height: auto;
    color: $color-12;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .star {
        display: flex;
        flex-direction: row;
        height: auto;
        width: 100%;
        align-items: center;

        > img {
            margin-right: 0.5em;
            width: 20px;
            height: 20px;
        }
        > label {
            font-size: 0.9vw;
        }

        .facilityAssigned {
            text-transform: capitalize;
        }
    }

    .hideStar {
        display: none;
    }

    > ul {
        list-style-type: none;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: auto;
        justify-content: flex-start;
        align-items: flex-start;
        padding-left: 0;
        margin-top: 3em;
    }
}
