@import "styles/colorPalette.scss";

.RemarksField {
  display: grid;
  grid-template-rows: 1fr;
  grid-auto-rows: auto;
  padding-top: 1%;
  padding-left: 3em;

  > span:nth-child(1) {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    height: 100%;
    align-items: center;

    > div:nth-child(1) {
      justify-content: center;
      flex: 1rem 2rem 1em;
      width: 40%;
      height: auto;
      margin-right: 2em;

      > label {
        color: $color-12;
      }
      > textarea {
        resize: none;
        width: 100%;
        height: 100px;
        border-radius: 5px;
        border: 0.5px solid $color-13;
      }
    }
    > div:nth-child(2) {
      justify-content: center;
      flex: 1rem 2rem 1em;
      width: 40%;
      height: auto;

      .errorClass {
        border: 1px solid red;
      }

      .error {
        color: red;
        font-size: 11px;
      }
      > label {
        color: $color-12;
      }
      > textarea {
        resize: none;
        width: 100%;
        height: 100px;
        border-radius: 5px;
        border: 0.5px solid $color-13;
      }
    }
  }
}

.errorMessage {
  padding: 0;
  margin: 0;
  color: red;
  font-size: 11px;
}
