@import "styles/colorPalette.scss";

.root {
  position: absolute;
  left: 0;
  top: 0%;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  -webkit-user-select: none; /* Chrome 49+ */
  -moz-user-select: none; /* Firefox 43+ */
  -ms-user-select: none; /* No support yet */
  user-select: none;

  .body {
    width: 100%;
    height: 100%;
    display: grid;
    flex-direction: column;
    grid-template-columns: 0.35fr 1.7fr;
    background-color: $color-1;
    padding-top: 2em;
    padding-bottom: 2em;
    // padding-top: 2em;
    // padding-bottom: 2em;
    // padding-left: 2.5em;
    // padding-right: 1em;
    // grid-gap: 2em;

    .leftSidebarWrapper {
      position: sticky;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      // align-items: center;

      .leftbodySideBar {
        width: 100%;
        // height: 85%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 0 2em;

        .profileImage {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 170px;
          // height: 130px;
          border-radius: 100%;
          border: 1px solid $color-2;
          // transform: translateY(-18%);
          margin-bottom: 2em;
          overflow: hidden;
          > img {
            width: 100%;
            height: 100%;
          }
        }
        .text {
          color: $color-12;
          padding-bottom: 1em;
          font-size: 1vw;
          text-decoration: none;

          .link {
            text-decoration: none;

            color: $color-12;
          }

          .link:focus {
            cursor: pointer;
            text-decoration: underline;
            font-weight: bolder;
          }
        }
      }
    }

    .rightSidebody {
      width: 100%;
      height: auto;
      // padding-top: 1em;
      // padding-left: 2em;
      display: flex;
      border-left: 1px solid #9ea3ac;
      padding: 0 2em;
      margin-bottom: 5em;

      .rightSidebodyWrapper {
        width: 100%;
        height: auto;
        flex: 1 1 1;
      }
    }
  }
}
