@import "styles/colorPalette.scss";

.root {
  position: absolute;
  left: 0;
  top: 0%;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;

  > div {
    padding: 0;
  }
  > div:nth-child(1) {
    position: sticky;
  }

  .FooterButtonContainer {
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    bottom: 0;
    height: auto;
    padding: 0;

    .FooterButtonWrapper {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      margin-top: 2%;
      width: 100%;
      height: auto;
      background-color: rgba(0, 0, 0, 0.3);
      padding-left: 3em;

      > div:nth-child(1) {
        padding-top: 1.5em;
        padding-bottom: 1.5em;
        .disapproveButton {
          padding-top: 0.5em;
          padding-bottom: 0.5em;
          padding-right: 3em;
          padding-left: 3em;
          text-transform: capitalize;
        }
      }
      > div:nth-child(2) {
        flex: 1;
      }
      > div:nth-child(3) {
        display: flex;
        justify-content: space-evenly;
        padding-top: 1.5em;
        padding-bottom: 1.5em;
        flex-direction: row;
        margin-right: 10em;

        > div:nth-child(1) {
          display: flex;
        }

        > div:nth-child(1) {
          margin-left: 1em;
          .approveButton {
            padding-top: 0.5em;
            padding-bottom: 0.5em;
            padding-right: 3em;
            padding-left: 3em;
            background-color: $color-9;
            color: $color-1;
            text-transform: capitalize;
          }
        }
      }
    }
  }
}

.dialogContainer {
  .btnGroup {
    display: flex;
    justify-content: center;
    .btnCancel {
      background: $color-10;
      color: $color-1;
      margin: 0 10px;
      text-transform: capitalize;
    }
    .btnApprove {
      background: $color-8;
      color: $color-1;
      margin: 0 10px;
      text-transform: capitalize;
    }
  }
}
