@import "styles/colorPalette.scss";

.editButton {
  position: absolute;
  display: flex;
  align-items: center;
  width: auto;
  height: auto;
  margin-right: 2.5em;
  right: 0;
  transform: translateY(-70px);
  z-index: 1;
  .buttonContainer {
    width: 100%;
    height: 100%;

    color: white;
    background-color: $color-9;
    .insideText {
      font-weight: bold;
      font-size: 8px;
      display: flex;
      flex-direction: column;
      > label {
        flex: 1 1 1;
        font-size: 0.6vw;
        padding-bottom: 0.3em;
        padding-top: 0.3em;
      }
    }
  }
}

.root {
  width: 100%;
  height: 100%;
  display: grid;
  flex-direction: column;
  grid-template-columns: 0.2fr 1fr 0.3fr;
  color: $color-3;

  .div-1 {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;

    .calllFlow {
      display: flex;
      flex-direction: row;

      > text {
        font-size: 120%;
        margin-right: 0.5em;
        font-weight: 600;
      }

      .editIcon {
        display: none;
        margin-top: 0.1em;
        width: 0.7em;
        height: 0.7em;
      }
    }

    .numberText {
      display: flex;
      margin-top: 0.5em;
      font-size: 100%;
      min-width: 130%;
    }

    .radioButtonsField {
      color: $color-3;
      width: 100%;
      height: auto;
    }
  }

  .div-2 {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .contactDetailsBody {
      width: 100%;
      height: 70%;
      display: flex;
      flex-direction: column;
      // overflow-y: scroll;

      .contactDetailsWrapper {
        flex-direction: row;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        height: auto;

        .contactDetailsBox {
          margin: 1em;
          width: 45%;
          height: auto;
          display: grid;
          grid-template-columns: 1.9fr 0.2fr;
          flex-direction: column;

          > div:nth-child(1) {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .designation {
              font-size: 140%;
              font-weight: bold;
            }
          }

          > div:nth-child(2) {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: flex-start;
            width: auto;
            height: auto;

            .icons {
              width: 20px;
              margin: 5px;
              transform: translateY(-22%);
            }
            .icons:hover {
              cursor: pointer;
              color: #f50057;
            }
          }
        }
      }
    }
  }

  .div-3 {
    width: 100%;
    height: auto;
    color: $color-9;

    > text:hover {
      cursor: pointer;
      font-weight: bolder;
    }
    > text:focus {
      cursor: pointer;
      font-weight: bolder;
    }
  }
}
.FooterButtonContainer {
  position: -webkit-sticky;
  position: fixed;
  width: 100%;
  padding: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,0,0,.3);
  height: 85px;

  .FooterButtonWrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    height: auto;
    padding-left: 3em;
    width: 367px;
    margin-left: auto;
    margin-top: 20px;

    > div:nth-child(1) {
      .cancelButton {
        padding-top: 0.75em;
        padding-bottom: 0.75em;
        padding-right: 3em;
        padding-left: 3em;
        text-transform: capitalize;
      }
    }
    > div:nth-child(2) {
      flex: 1;
    }
    > div:nth-child(3) {
      display: flex;
      justify-content: space-evenly;
      flex-direction: row;
      margin-right: 5em;

      > div:nth-child(1) {
        display: flex;

        .SaveButton {
          display: grid;
          flex-direction: row;
          padding-right: 1.5em;
          padding-left: 1.5em;
          background-color: rgba(38, 194, 129, 1);
          color: white;
        }
      }

      > div:nth-child(1) {
        margin-left: 1em;
        .SaveBtn {
          padding-top: 0.5em;
          padding-bottom: 0.5em;
          padding-right: 3em;
          padding-left: 3em;
          background-color: rgba(38, 194, 129, 1);
          color: white;
          text-transform: capitalize;
        }
      }
    }
  }
}
