@import "styles/colorPalette.scss";

.textField {
  width: 100%;
  height: 100%;
  padding: 0;
}

.rootTable {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;

  .TableHeadRoot {
    width: 100%;
    height: auto;
    background-color: $color-2;
    -webkit-user-select: none; /* Chrome all / Safari all */
    -moz-user-select: none; /* Firefox all */
    -ms-user-select: none; /* IE 10+ */
    user-select: none;

    .tableHeadRow {
      color: $color-3;
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: row;

      th{
        padding:0 10px;
      }

      th:nth-child(1) {
        width: 8.2%;
      }
      th:nth-child(2) {
        width: 14%;
      }
      th:nth-child(3) {
        width: 20%;
      }
      th:nth-child(4) {
        width: 21%;
      }
      th:nth-child(5) {
        width: 20%;
      }
      th:nth-child(6) {
        width: 18%;
      }

      .tableCell {
        margin-top: 1em;
        font-size: 0.9vw;
        width: auto;
        height: auto;
        .noTextField {
          height: auto;
          padding: 0;
        }
        .withTextfield {
          padding: 0;
          height: auto;

          .showIcon {
            display: inline-block;
            position: absolute;

            .sortIcon {
              margin-left: 2em;
              display: inline-block;

              & :hover {
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }

  .tableBody {
    height: 34vh;
    width: 100%;
    overflow-y: scroll;
    padding: 0;
    background-color: white;

    .tableRow {
      color: $color-3;
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: row;
      
      .tableCell {
        font-size: 0.9vw;
        width: auto;
        height: auto;
      }

      td{
        padding:0 10px;
      }

      td:nth-child(1) {
        width: 8.2%;
      }
      td:nth-child(2) {
        width: 14.3%;
      }
      td:nth-child(3) {
        width: 20.3%;
      }
      td:nth-child(4) {
        width: 21.1%;
        max-width: 21.1vw;
        overflow-wrap: break-word;
      }
      td:nth-child(5) {
        width: 20.2%;
        max-width: 20.2vw;
        overflow-wrap: break-word;
      }
      td:nth-child(6) {
        width: 17.2%;
      }
    }
  }
}

