.paginationDiv {
  height: 40% !important;
  width: 100%;
  display: flex;

  .pageCount {
    flex: 1;
  }

  .Pagination {
    flex: 1;
    text-align: right;
    padding-right: 1em;
  }
}

.tableRow:nth-of-type(even) {
  background-color: white;
}
.tableRow:nth-of-type(odd) {
  background-color: $color-1;
}

