@import "styles/colorPalette.scss";

.root {
  width: 100%;
  height: 100%;
  position: relative;

  .container {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1.2fr 0.8fr;
    grid-gap: 1.5em;

    .viewFile {
      flex-shrink: 30%;
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-rows: 0.2fr 1.8fr;
      flex-direction: row;
      background-color: $color-2;
      padding-left: 0.5em;
      padding-right: 0.5em;

      .topText {
        display: flex;
        width: 100%;
        height: 8vh;
        justify-content: flex-start;
        align-items: center;
        padding-left: 2em;
        color: $color-3;
        font-weight: 600;
      }
    }

    .fileDrawer {
      width: 100%;
      height: 75%;

      .editDelete {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 4vh;
        font-size: 0.8vw;
        width: 25%;
        margin-right: 1.5em;
        border-radius: 10px;
        font-weight: bolder;
        color: white;
        background-color: $color-10;
        margin-bottom: 1em;
        > text {
          width: 120%;
          height: 100%;
        }
      }

      .editAdd {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 4vh;
        font-size: 0.8vw;
        width: 25%;
        border-radius: 10px;
        background-color: $color-8;
        color: white;
        font-weight: bolder;
        margin-bottom: 1em;
      }
    }
  }
}
