@import 'styles/colorPalette.scss';

.container {
  margin: 1rem 0 0.5rem;
  padding-left: 2.5rem;
  padding-right: 2.5rem;

  .wrapper {
    display: flex;
    flex-direction: column;
    gap: 8px;

    .title {
      font-size: 24px;
      font-weight: 500;
      color: $color-12;
    }

    .label {
      color: $color-12;
    }

    textarea {
      resize: none;
      border-radius: 5px;
      border: 0.5px solid $color-13;
      padding: 0.5rem;
    }
  }
}
