@import "styles/colorPalette.scss";



.titleWrapper{
  padding-left: 3em;
  .dentistText {
    font-size: 24px;
    font-weight: 500;
    width: 100%;
    height: 30px;
    margin-bottom: 1em;
  }
}

.dentistInformation {
  display: grid;
  flex-direction: row;
  grid-template-rows: 0.3fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-rows: minmax(100%, auto);
  height: 100%;
  width: 100%;
  grid-gap: 0.5em;

  color: $color-12;
  padding-left: 3em;
  .dentistInputFields {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 0;
    width: 100%;
    height: 100%;

    .FirstName,
    .middleName,
    .lastName {
      width: 26.5%;
      margin-right: 2.5em;
    }

    .suffix {
      width: 10%;
      margin-right: 2.5em;
    }
    .birthDate {
      width: 13.4%;
      margin-right: 2.5em;
      padding-top: 0.5em;
      .editInputWidth {
        width: 100%;
        padding: 12px;

        input {
          padding: 0 !important;
          font-size: 0.9vw;
        }
      }
    }
    .civilStatus,
    .gender {
      width: 12%;
      margin-right: 2.5em;
      padding-bottom: 1.6em;
    }
    .emailAddress,
    .contact {
      width: 26.5%;
      margin-right: 2.5em;
    }
    .main,
    .major {
      width: 26.5%;
      margin-right: 2.5em;
    }
    .sub,
    .other,
    .pda,
    .prc,
    .phil,
    .tin {
      width: 26.5%;
      margin-right: 2.5em;
    }
    .prcExpiry,
    .philExpiry {
      padding-top: 0.5em;
    }
  }
}
