@import "styles/colorPalette.scss";

.changeName {
    display: flex;
    flex-direction: row;
    min-width: 100%;
    height: auto;
    color: $color-12;
    font-size: 23px;

    .TextField {
        width: 100%;
        height: auto;
    }

    .editIcon {
        display: flex;
        margin-right: 0.5em;
    }
}

.root {
    display: flex;
    flex-direction: column;
    height: auto;
    width: 100%;

    color: $color-12;

    .wrapper {
        width: 100%;
        height: auto;

        .listStyles {
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;
            margin-bottom: 1.5em;
            text-transform: uppercase;

            .firstRow {
                width: 60%;
                height: auto;
                display: flex;
                flex-direction: row;

                > label:nth-child(1) {
                    font-size: 0.9vw;

                    width: 45%;
                    height: auto;
                    color: rgba(31, 58, 147, 0.5);
                }

                > label:nth-child(2) {
                    font-size: 0.9vw;

                    width: 75%;
                    height: auto;
                    color: $color-12;
                }
            }

            .secondRow {
                margin-left: 2em;
                width: 40%;
                height: auto;
                display: flex;
                flex-direction: row;

                > label:nth-child(1) {
                    font-size: 0.9vw;

                    width: 32%;

                    height: auto;
                    color: rgba(31, 58, 147, 0.5);
                }

                > label:nth-child(2) {
                    font-size: 0.9vw;

                    width: 65%;
                    height: auto;
                    color: $color-12;
                }
            }
        }

        .listStyles:nth-child(4),
        .listStyles:nth-child(16) {
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;
            margin-bottom: 1.5em;
            text-transform: uppercase;

            .firstRow {
                min-width: 100%;
                height: auto;
                display: flex;
                flex-direction: row;

                > label:nth-child(1) {
                    font-size: 0.9vw;

                    width: 28.5%;
                    height: auto;
                    color: rgba(31, 58, 147, 0.5);
                }

                > label:nth-child(2) {
                    font-size: 0.9vw;

                    width: 100%;
                    height: auto;
                    color: $color-12;

                    display: flex;
                    flex-direction: row;
                }
            }
        }
    }
}
