@import "styles/colorPalette.scss";

.textField {
  width: 100%;
  height: 100%;
  padding: 0;
}
.inputField {
  width: 100%;
  height: 30px;
  border-radius: 5px;
  border: 1px solid grey;
  padding-left: 0.5em;
  color: $color-12;
}

.rootTable {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  height: auto;

  overflow-y: scroll;

  .tableHead {
    width: 100%;
    height: auto;
    background-color: $color-2;
    -webkit-user-select: none; /* Chrome all / Safari all */
    -moz-user-select: none; /* Firefox all */
    -ms-user-select: none; /* IE 10+ */
    user-select: none;

    .tableHeadRow {
      color: $color-3;
      width: 90%;
      height: auto;

      th:nth-child(1) {
        width: 8.5%;
      }

      th:nth-child(2) {
        width: 15%;
      }

      th:nth-child(3) {
        width: 25%;
      }

      th:nth-child(4) {
        width: 25%;
      }

      .tableCell {
        font-size: 1vw;
        width: auto;
        height: auto;
        //  background-color: pink;

        .noTextField {
          transform: translateY(-65%);
        }
        .withTextfield {
          transform: translateY(20%);
          padding: 0;

          height: auto;

          .showIcon {
            display: inline-block;
            position: absolute;

            .sortIcon {
              margin-left: 2em;
              display: inline-block;
            }
          }
        }
        & :hover {
          cursor: pointer;
        }
      }
    }
  }

  .tableBodyRoot {
    width: 100%;
    height: 34vh;

    .tableBody {
      height: 34vh;
      width: 95.12%;
      position: absolute;
      overflow-y: scroll;
      padding: 0;
      background-color: white;

      .tableRow {
        width: auto;
        height: auto;
        position: relative;
        margin: 0;

        .tableCell {
          font-size: 0.9vw;
          width: 100vw;
          height: auto;
        }

        th:nth-child(1) {
          width: 8.5%;
        }

        th:nth-child(2) {
          width: 15%;
        }

        th:nth-child(3) {
          width: 25.4%;
        }

        th:nth-child(4) {
          width: 24.5%;
        }
        th:nth-child(5) {
          width: 15%;
        }
      }
    }
  }
}

.pageCount {
  flex: 1.5;
}

.emptySpace {
  flex: 3;
}

.Pagination {
  flex: 1;
}

.tableRow:nth-of-type(even) {
  background-color: white;
}
.tableRow:nth-of-type(odd) {
  background-color: $color-1;
}

@media screen and (max-width: 1280px) {
  .rootTable {
    .tableHead {
      .tableHeadRow {
        th:nth-child(1) {
          width: 10%;
        }

        th:nth-child(2) {
          width: 15%;
        }

        th:nth-child(3) {
          width: 22%;
        }

        th:nth-child(4) {
          width: 22%;
        }
      }
    }

    .tableBodyRoot {
      .tableBody {
        width: 93%;

        .tableRow {
          th:nth-child(1) {
            width: 10%;
          }

          th:nth-child(2) {
            width: 16%;
          }

          th:nth-child(3) {
            width: 22%;
          }

          th:nth-child(4) {
            width: 22%;
          }
        }
      }
    }
  }

  .nativeSelect {
    font-size: 0.8vw;
  }

  .pageCount {
    flex: 3 !important;
  }

  .emptySpace {
    flex: 2 !important;
  }

  .Pagination {
    flex: 2 !important;
  }
}
