@import "styles/colorPalette.scss";

.root {
  width: auto;
  height: 800px;
  overflow: auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  background-color: transparent;
  margin-left: 5.8em;
  padding-left: 0;
  border-color: white;
  border-left: white;

  .container {
    margin-top: 3em;
    // margin-right: 0.5em;
    // width: 100%;
    height: 60%;
    display: grid;
    flex-direction: row;
    grid-template-rows: 0.3fr 1fr 0.2fr 0.4fr;

    > div:nth-child(1) {
      background-color: $color-4;
      width: 100%;
      height: auto;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      padding-bottom: 0.4em;
      padding-right: 3em;
      .button {
        margin-top: 2em;
        background-color: $color-9;
        color: white;
        text-transform: initial;
        padding-left: 1.5em;
        padding-right: 1.5em;
        font-size: 0.8vw;
      }
    }

    > div:nth-child(2) {
      background-color: $color-4;
      width: 100%;
      height: auto;
      display: flex;
    }
    > div:nth-child(3) {
      background-color: $color-4;
      width: 100%;
      height: auto;
      display: flex;
    }

    > div:nth-child(3) {
      background-color: transparent;
      width: 100%;
      height: auto;
      display: flex;
      justify-content: flex-end;
      align-items: flex-start;
      padding-top: 0.4em;
      padding-right: 0.5em;
      margin-bottom: 5em;
      .button {
        background-color: $color-10;
        color: white;
        text-transform: initial;
        padding-left: 2.5em;
        padding-right: 2.5em;
        font-size: 1vw;
      }
      .btnCancel {
        background-color: $color-10;
        color: white;
        text-transform: initial;
        padding-left: 2.5em;
        padding-right: 2.5em;
        font-size: 1vw;
      }
      .btnSave {
        margin-top: 10px;
        background-color: $color-9;
        color: white;
        text-transform: initial;
        padding-left: 2.5em;
        padding-right: 2.5em;
        margin-left: 1.5em;
        font-size: 1vw;
      }
    }

    .closeButton {
      height: auto;
      display: flex;
      justify-content: flex-end;
      background-color: $color-10;
      color: white;
      text-transform: initial;
      padding-left: 2.5em;
      padding-right: 2.5em;
      font-size: 1vw;
      float: right;
      margin-top: 10px;
    }
  }
}
