@import "styles/colorPalette.scss";

.headerContainer {
  position: relative;
  width: 100%;
  height: auto;
  display: grid;
  padding: 0;
  grid-template-rows: 1fr 1fr;
  grid-gap: 0.2em;
  grid-auto-rows: minmax(200px, auto);
  z-index: 10;
  background-color: #ffffff;

  .breadCrumbs {
    height: auto;
    padding: 0 20px;
  }

  .secondContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    // padding-left: 1.5em;

    > div:nth-child(1) {
      padding: 0 20px 15px;
      font-weight: bold;
      font-size: 30px;
      color: $color-12;
    }
  }
}
