@import "styles/color.scss";

.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .label {
    padding-bottom: 5px;
  }

  .wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    margin-bottom: 1em;
    
    .read-only-value {
      font-weight: unset;
      padding: 10px;
    }

    .text-field {
      flex: 1;
      margin-bottom: 23px;
      display: flex;

      .form-control {
        width: 100%;
      }

      &.discrepancy {
        background-color: $color-19;
        .divider {
          background: $color-20;
        }
        .end-adornment {
          color: $color-21;
        }
      }

      .input {
        width: 100%;
        * {
          padding: 10px;
        }

        
      }

      .input-amount {
        width: 100%;
        * {
          padding: 10px;
          text-align: right;
        }
      }

      .end-adornment {
        color: #8b8b8b;
        font-weight: 500;
        padding: 10px;
      }

      .select {
        color: #000000;
        padding: 4px;
        padding-left: 10px;
      }

      // Errors
      &.has-error {
        border: 1px solid red;
        margin-bottom: unset;
      }
    }

    .errorField {
        width: 100%;
        height: auto;
        display: flex;
        text-align: center;

      span.error {
        
         color: red;
         width: 100%;
         font-size: 11px !important;
         overflow-wrap: break-word;
       }
    }

  }
}
