@import "styles/colorPalette.scss";
.root {
    .title {
        color: $color-12;
        margin-bottom: 0;
    }
    .content {
        width: 100%;
        padding: 20px;
        padding-top: 0;
        .row {
            display: flex;
            .fieldContainer {
                margin-right: 2rem;
                .fieldLabel {
                    color: $color-4;
                    margin-bottom: 0;
                }
                .fieldContent {
                    color: $color-12;
                    font-weight: normal;
                    margin-top: 0;
                }
            }
        }
    }
}
