@import "styles/colorPalette.scss";

.root {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    padding-left: 5.8em;

    .container {
        width: 100%;
        height: 60%;
        display: flex;
        flex-direction: column;

        > div:nth-child(1) {
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: column;

            > div:nth-child(1) {
                background-color: $color-4;
                width: 100%;
                height: auto;
                display: flex;
                flex-direction: column;
            }
            > div:nth-child(2) {
                width: 100%;
                height: auto;
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                align-items: flex-start;
                padding-top: 0.6em;
                padding-bottom: 0.6em;
                background-color: $color-4;
            }
        }

        .closeButton {
            height: auto;
            display: flex;
            justify-content: flex-end;
            background-color: $color-10;
            color: white;
            text-transform: initial;
            padding-left: 2.5em;
            padding-right: 2.5em;
            font-size: 1vw;
            float: right;
            margin-top: 10px;
        }
    }
}
