@import "styles/colorPalette.scss";

.root {
    display: flex;
    flex-direction: column;
    height: auto;
    width: 100%;

    color: $color-12;

    .FacilityAssignments {
        width: 100%;
        height: auto;
        margin-bottom: 18px;

        > label {
            color: rgba(31, 58, 147, 0.5);
            font-size: 16px;
            margin-left: 0.5em;
        }
        .star {
            width: 10px;
            height: 10px;
        }

        .radioGroup {
            display: flex;
            width: 100%;
            height: auto;
            flex-direction: row;
            .errorMessage {
                font-size: 0.75rem;
                padding-left: 12em;
                color: red;
                // margin-left: 9.5em;
            }
        }

        .formComponent {
            width: 100%;
            height: auto;
            padding-left: 10.7em;
            .formGroup {
                width: 100%;
                height: auto;

                .checkLabel {
                    text-transform: initial;
                    font-size: 12px;
                }
                .exclusiveTo {
                    width: 100%;
                    height: auto;
                    display: flex;
                    align-items: center;
                    .label {
                        font-size: 15px;
                        margin-left: 1em;
                        margin-right: 1em;
                    }

                    .exclusiveToField {
                        display: flex;
                        .inputField {
                            width: auto;
                            height: 25px;
                            border-radius: 5px;
                            border: 1px solid grey;
                            padding-left: 0.5em;
                            color: $color-12;
                        }
                    }
                    .exclusiveToFieldHide {
                        display: none;
                    }
                }
            }
        }
    }

    .medicalPartnerAndTvs {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;
        margin-bottom: 18px;
        .medicalPartnerStatus {
            height: auto;
            width: 40%;
            > label {
                font-size: 16px;
                text-transform: uppercase;
                color: rgba(31, 58, 147, 0.5);
            }

            .medicalRadio {
                width: 100%;
                height: auto;

                .radioGroup {
                    width: 100%;
                    height: auto;

                    .radioLabel {
                        height: auto;
                        width: 100%;
                        font-size: 12px;
                        margin-bottom: 0 !important;
                    }
                }
            }
            .onHoldWrapper {
                display: flex;
                align-items: center;

                .radioLabel2 {
                    height: auto;
                    // width: 100%;
                    margin-right: 30px;
                    font-size: 12px;
                    margin-bottom: 0 !important;
                }
                span > label {
                    margin-right: 1em;
                    font-size: 15px;
                }

                .reasonForOnhold {
                    display: flex;
                    .inputField {
                        width: auto;
                        height: 25px;
                        border-radius: 5px;
                        border: 1px solid grey;
                        padding-left: 0.5em;
                        color: $color-12;
                    }
                }
                .reasonForOnholdHide {
                    display: none;
                }
            }
        }
        .Tvs {
            margin-left: 3.5em;
            height: auto;

            > label {
                color: rgba(31, 58, 147, 0.5);
            }
            font-size: 16px;
            > sub {
                font-size: 10px;
            }

            .medicalRadio {
                width: 100%;
                height: auto;

                .radioGroup {
                    width: 100%;
                    height: auto;

                    .radioLabel {
                        height: auto;
                        width: 100%;
                        font-size: 12px;
                        margin-bottom: 0 !important;
                    }
                }
            }
            .errorMessage {
                font-size: 0.75rem;
                padding-left: 5px;
                color: red;
            }
        }
    }
    .wrapper {
        width: 100%;
        height: auto;

        .labelField {
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;

            > label {
                width: 250px;
                height: auto;
                font-size: 16px;
                text-transform: uppercase;
                color: rgba(31, 58, 147, 0.5);
                margin-bottom: 1.5em;
            }

            .textDiv {
                width: 100%;
                height: auto;
                margin-left: 3em;

                .inputField {
                    width: 25%;
                    height: 25px;
                    border-radius: 5px;
                    border: 1px solid grey;
                    padding-left: 0.5em;
                    color: $color-12;
                    background-color: white;

                    input::-webkit-outer-spin-button,
                    input::-webkit-inner-spin-button {
                        -webkit-appearance: none;
                        margin: 0;
                    }

                    /* Firefox */
                    input[type="number"] {
                        -moz-appearance: textfield;
                    }
                }
                .errorMessage {
                    font-size: 0.75rem;
                    padding-left: 5px;
                    color: red;
                }
            }
        }
    }
}
