@import "styles/colorPalette.scss";

.root {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .roomTypeContainer {
        background-color: #dddfe3;
        border: none;
        outline: none;
        min-width: 900px;
        height: 300px;
        padding: 20px;
        .formGroup {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .inputGroup {
                display: flex;
                .inputContainer {
                    flex: 1;
                }
            }
            .buttonGroup {
                margin-top: 20px;
                display: flex;
                justify-content: flex-end;
            }
        }
    }
}
