@import "styles/colorPalette.scss";

.facilityInformationField {
  display: grid;
  grid-template-rows: 0.2fr 1.8fr;
  grid-auto-rows: minmax(100%, auto);
  width: 100%;
  height: 100%;
  padding-top: 2em;
  padding-left: 2.5em;
  padding-bottom: 0;
  font-weight: 500;

  color: $color-12;

  > div:nth-child(1) {
    width: 100%;
    height: 50px;
    color: $color-12;
    font-weight: 400;
    font-size: 24px;

    > label {
      font-size: 13px;
    }
  }

  .textFieldArea {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    justify-content: center;
    align-items: flex-start;

    > div {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
    }

    .firstLayerInputs {
      width: 100%;
      height: auto;
      .facilityInput {
        width: 45%;
        height: auto;
        margin-right: 2.5em;
      }
      .medicalPartnerId {
        width: 20%;
        height: auto;
      }
    }

    .SecondLayerInputs {
      width: 100%;
      height: 100%;
      margin-bottom: 1em;

      .addressInput {
        width: 45%;
        height: auto;
        padding-bottom: 2em;
        position: relative;
        .AddressField {
          width: 100%;
          // height: 1vh;
          outline: none;
          border-color: white;
          border-radius: 5px;
        }
      }
    }

    .ThirdLayerInputs {
      width: 100%;
      height: auto;

      .roomNumber {
        width: 15%;
        height: auto;
        margin-right: 2.5em;
      }
      .contactNumber {
        width: 28%;
        height: auto;
        margin-right: 2.5em;
      }
      .Clinic-Schedule-div {
        width: 35%;
        height: auto;
      }
    }

    .FourthLayerInputs {
      width: 100%;
      height: auto;

      .coordinators-input {
        width: 45%;
        height: auto;
        margin-right: 2.5em;
      }
      .Contact-Number {
        width: 20%;
        height: auto;
        > label {
          color: transparent;
          user-select: none;
        }
      }
    }

    .FifthLayerInputs {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;

      .namenameOfAssitantFields {
        display: flex;
        flex-direction: row;
        margin-right: 2.5em;
        width: 100%;
        height: auto;

        > div:nth-child(1) {
          width: 45%;
          height: auto;
          margin-right: 2.5em;
        }
        > div:nth-child(2) {
          width: 20%;
          height: auto;
          margin-right: 2.5em;
        }
      }

      .AdditionalNameOfAssistantFields {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: auto;

        .AddAssistantWrapper {
          display: flex;
          flex-direction: row;
          width: 100%;
          height: auto;

          > div:nth-child(1) {
            width: 45%;
            height: auto;
            margin-right: 2.5em;
          }
          > div:nth-child(2) {
            width: 20%;
            height: auto;
          }
          .delete {
            padding-top: 1em;
            margin-left: 1em;
            color: $color-10;

            &:hover {
              cursor: pointer;
              font-weight: bold;
            }
          }

          .hideDelete {
            display: none;
          }
        }
      }
    }
  }
  .addButtonAssitant {
    text-decoration: underline;
    color: $color-9;

    > label:hover {
      cursor: pointer;
      font-weight: bold;
    }
  }
}

span.error {
  padding-top: 1px;
  color: red;
  font-size: 11px;
  position: absolute;
  bottom: -10px;
}
